    loadCardPrices = () => {
        const axiosConfig = api.axiosConfig
        let {cardsToFetch,displayCardNamesHash,mainboard,sideboard,maybeboard,commanders,companions} = this.state
        axios({
            method: 'post',
            url: api.shopbotUrl + '/read',
            data: {
                "cardHash":cardsToFetch
            }, headers:axiosConfig.headers
        }).then(res => {
            let {priceHash} = this.state
            if (res.status === 200 ) {

                let priceRangesHash = {'0 < 1':[],'1 < 2':[],'2 < 5':[],'5 < 10':[],'10 < 20':[],'> 20':[]}
                for(let decklisttuple of mainboard){
                  const displayName = displayCardNamesHash[decklisttuple.cardname]
                  if (res.data.hasOwnProperty(decklisttuple.cardname)){
                      priceHash[decklisttuple.cardname] = res.data[decklisttuple.cardname]
                      
                      if (res.data[decklisttuple.cardname] <= 1){
                        priceRangesHash['0 < 1'].push(decklisttuple)
                      } else if (res.data[decklisttuple.cardname] <= 2){
                        priceRangesHash['1 < 2'].push(decklisttuple)
                      } else if (res.data[decklisttuple.cardname] <= 5){
                        priceRangesHash['2 < 5'].push(decklisttuple)
                      } else if (res.data[decklisttuple.cardname] <= 10){
                        priceRangesHash['5 < 10'].push(decklisttuple)
                      } else if (res.data[decklisttuple.cardname] <= 20){
                        priceRangesHash['10 < 20'].push(decklisttuple)
                      } else {
                        priceRangesHash['> 20'].push(decklisttuple)
                      }
                  } else if (res.data.hasOwnProperty(displayName)){
                      priceHash[decklisttuple.cardname] = res.data[displayName]
                      if (res.data[displayName] <= 1){
                        priceRangesHash['0 < 1'].push(decklisttuple)
                      } else if (res.data[displayName] <= 2){
                        priceRangesHash['1 < 2'].push(decklisttuple)
                      } else if (res.data[displayName] <= 5){
                        priceRangesHash['2 < 5'].push(decklisttuple)
                      } else if (res.data[displayName] <= 10){
                        priceRangesHash['5 < 10'].push(decklisttuple)
                      } else if (res.data[displayName] <= 20){
                        priceRangesHash['10 < 20'].push(decklisttuple)
                      } else {
                        priceRangesHash['> 20'].push(decklisttuple)
                      }
                  }
                }
                for(let decklisttuple of sideboard){
                    const displayName = displayCardNamesHash[decklisttuple.cardname]
                    if (res.data.hasOwnProperty(displayName)){
                        priceHash[decklisttuple.cardname] = res.data[displayName]
                    }
                }
                for(let decklisttuple of maybeboard){
                    const displayName = displayCardNamesHash[decklisttuple.cardname]
                    if (res.data.hasOwnProperty(displayName)){
                        priceHash[decklisttuple.cardname] = res.data[displayName]
                    }
                }
                for(let decklisttuple of commanders){
                    const displayName = displayCardNamesHash[decklisttuple.cardname]
                    if (res.data.hasOwnProperty(displayName)){
                        priceHash[decklisttuple.cardname] = res.data[displayName]
                    }
                }
                for(let decklisttuple of companions){
                    const displayName = displayCardNamesHash[decklisttuple.cardname]
                    if (res.data.hasOwnProperty(displayName)){
                        priceHash[decklisttuple.cardname] = res.data[displayName]
                    }
                }
                this.setState({priceHash,priceRangesHash,loadedPrices:true})
            }
        }).catch(err => {
            console.log(err)
            console.log(err.response)
        })
    }





    findBestCombination = async () => {
        let {textsEn,textsPt} = this.state

        textsPt.loadingMessage = "Buscando preos nas lojas... Isto pode demorar um pouco, vamos pegar um cafzinho?"
        textsEn.loadingMessage = "Fetching card prices on stores, this may take a while, how about a coffe break?"

        this.setState({loadingDisplay:'block',clickedButton1:true,textsEn,textsPt})
        const axiosConfig = api.axiosConfig
        let data = {
            "cardList":this.state.deckString
        }

        await axios({
            method: 'post',
            url: api.shopbotUrl + '/update',
            data: data, headers:axiosConfig.headers
        }).then( res => {
            textsPt.loadingMessage = "Buscando a melhor combinao de lojas, pode demorar de 1 a 5 minutinhos"
            textsEn.loadingMessage = "Fetching the best combination of stores to buy, this can take about 5 minutes"
            this.setState({textsEn,textsPt,loadingBarColor:'#FF851B'})
        })
        this.loadCardPrices()
        if (this.state.ispremium===true){
            await axios({
                method: 'post',
                url: api.shopbotUrl + '/solver',
                data: data, headers:axiosConfig.headers, timeout:900000
            }).then(res => {
                if (res.status === 200 ) {
                    textsPt.loadingMessage = "Carregando..."
                    textsEn.loadingMessage = "Loading..."
                    if (res.data.status===1){
                        this.setState({textsEn,textsPt, loadingBarColor:'#8884d8',lastValue:res.data.finalValue,stores:res.data.stores,displayStrsTabs:"block",loadingDisplay:'none'})
                    }else{
                        this.setState({textsEn,textsPt, loadingBarColor:'#8884d8',lastValue:-1,stores:[],loadingDisplay:'none'})
                    }
                }
            }).catch(err => {
                console.log(err)
                console.log(err.response)
                this.setState({loadingDisplay:'none'})
            })
        }else{
            await axios({
                method: 'post',
                url: api.shopbotUrl + '/solverTwo',
                data: data, headers:axiosConfig.headers, timeout:900000
            }).then(res => {
                if (res.status === 200 ) {
                    textsPt.loadingMessage = "Carregando..."
                    textsEn.loadingMessage = "Loading..."
                    if (res.data.solverOne.status===1&&res.data.solverMulti.status===1){
                        if (res.data.solverMulti.finalValue<res.data.solverOne.finalValue){
                            let keys = Object.keys(res.data.solverMulti.stores)
                            let qtdeLojas = keys.length.toString()
                            let diff = res.data.solverOne.finalValue-res.data.solverMulti.finalValue
                            let keysOne = Object.keys(res.data.solverOne.stores)
                            diff = diff.toFixed(2).toString()

                            textsEn.promoteTitle="Hey, you can purchase this deck on "+qtdeLojas+' stores by '+ res.data.solverMulti.finalValue.toFixed(2).toString()+' (shipping included) and save '+diff+' bucks'
                            textsPt.promoteTitle="D pra comprar o seu deck em "+qtdeLojas+' lojas por '+ res.data.solverMulti.finalValue.toFixed(2).toString()+ ' (frete incluso) e economizar '+diff+' reais'
                            textsEn.promoteMessage2="The store wich your deck is cheaper is "+keysOne[0]+", it will cost you "+res.data.solverOne.finalValue.toFixed(2).toString()+" dolars"
                            textsPt.promoteMessage2="A loja que o deck  mais barato  a "+keysOne[0]+", "+"ele vai custar "+res.data.solverOne.finalValue.toFixed(2).toString()+" R$"
                            textsEn.promoteMessage3="But purchasing from "+qtdeLojas+" stores the deck will cost "+res.data.solverMulti.finalValue.toFixed(2).toString()+" dolars, shipping included"
                            textsPt.promoteMessage3="Mas comprando de "+qtdeLojas+" lojas o deck vai custar "+res.data.solverMulti.finalValue.toFixed(2).toString()+" R$ (frete incluso) "


                            this.setState({textsEn,textsPt, loadingBarColor:'#8884d8',lastValue:res.data.solverOne.finalValue,stores:res.data.solverOne.stores, bestValue:res.data.solverMulti.finalValue,bestStores: res.data.solverMulti.stores,displayStrsTabs:"block",loadingDisplay:'none', promoteModalIsOpen:true})
                        }else{
                            this.setState({textsEn,textsPt, loadingBarColor:'#8884d8',lastValue:res.data.solverOne.finalValue,stores:res.data.solverOne.stores, bestValue:res.data.solverMulti.finalValue,bestStores: res.data.solverMulti.stores,displayStrsTabs:"block",loadingDisplay:'none', promoteModalIsOpen:false})
                        }
                    }else{
                        this.setState({textsEn,textsPt, loadingBarColor:'#8884d8',lastValue:-1,stores:[],loadingDisplay:'none'})
                    }

                }
            }).catch(err => {
                console.log(err)
                console.log(err.response)
                this.setState({loadingDisplay:'none'})
            })
        }
    }

