    copyDeck = async () => {
        let {loadedDecklistTuples} = this.state
        this.setState({clicledCopyDeck:true})
        let deckname = loadedDecklistTuples[0].deckname.trim()
        await this.insertDeck(localStorage.getItem('mdtUser'),deckname.trim(),loadedDecklistTuples[0].maincard, loadedDecklistTuples[0].maincard2,loadedDecklistTuples[0].colors,loadedDecklistTuples[0].f0rmat,loadedDecklistTuples[0].description,0,this.getCurrentDate(),0 )
        if (this.state.deckInsertOk===false){
            this.removeTrashDecks()
            return;
        }
        deckname = this.state.deckname

        await this.insertDeckList(deckname.trim(),this.getCurrentDate(), this.state.mainboard, this.state.sideboard, this.state.maybeboard)
        if (this.state.deckInsertOk===false){
            this.removeTrashDecks()
            return;
        }
        try{
            await this.insertDeckFolder(loadedDecklistTuples[0].f0rmat, localStorage.getItem('mdtUser'), deckname.trim(), 0)
        }catch(e){
            console.log(e)
            this.removeTrashDecks()
        }
        this.removeTrashDecks()
        this.props.history.push('/users/'+localStorage.getItem('mdtUser')+'/decks/'+this.changeDeckName(deckname.trim()))
        document.location.reload()
      }

