import BottomBar from "../../components/bottomBar"
import ErrorPage from "../ErrorPage"
import React, {Component} from 'react'
import {withRouter} from "react-router"
import {Link} from 'react-router-dom';
import api from "../../services/api"
import './style.css'
import GlobalNavBar from "../../components/globalNavBar"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ReactLoading from 'react-loading';
import Modal from 'react-modal';

const axios = require('axios')

function isLoggedUser(userNameString){
    if (userNameString===null||userNameString===undefined||localStorage.getItem('mdtUser')===null||localStorage.getItem('mdtUser')===undefined){
        return false;
      }
    if (userNameString.trim()===localStorage.getItem('mdtUser').trim()){
      return true;
    }
    return false
  }

function userOnLocalStorage(){
    if (localStorage.getItem('mdtUser')===null||localStorage.getItem('mdtUser')===undefined||localStorage.getItem('mdtToken')===null||localStorage.getItem('mdtToken')===undefined){
        return false
    }
    return true
}
class BecomeProPage extends Component{

    state = {
        waitPaypalClicked:false,
        loadedUser:undefined,
        problemOnLoad:false,
        donationLinkPt:"https://apoia.se/commanderdecks",
        textsEn:{
            "apoiaseWarning":"Payments made on apoia.se will take between 1 to 3 days for us to process",
            "alternative":"OR... click on Discover+Video, that will display the best buy combination and an show you a nice cards video",
            "payApoia":"Help on apoia.se",
            "paypalPay":"Help with paypal",
            "waitPaypal":"Please wait while paypal loads...",
        },
        textsPt:{
            "waitPaypal":"Por favor espere enquanto o paypal carrega...",
            "payApoia":"Ajude pelo apoia.se",
            "paypalPay":"Ajude via Paypal",
            "apoiaseWarning":"Contribuies feitas no apoia.se podem levar de 1 a 3 dias para serem processadas",
            "alternative":"OU... clique em Descobrir+Video, que mostrar a melhor combinao de compra e te mostrar um vdeo com cartas boas e baratas",
            
        },
    }

    constructor(props){
        super(props)
        

    }

    paypalMessage = () => {
        if (this.state.waitPaypalClicked===true){
            return (
                <div style={{padding:'3px',textAlign:'center',backgroundColor:'lightgray',fontWeight:'800'}}>{localStorage.getItem('countryCode')==='+55'?this.state.textsPt.waitPaypal:this.state.textsEn.waitPaypal}</div>
            )
        }
    }

    getPaypalLink = async() => {
        if (localStorage.getItem('mdtUser')===null||localStorage.getItem('mdtUser')==undefined){
            return <></>
        }
        const axiosConfig = api.axiosConfig
        let headers = axiosConfig.headers
        headers['Access-Control-Allow-Origin']='*'
        
        let data = {
            "value":20.0,
            "currencycode":localStorage.getItem('countryCode')==='+55'?'BRL':'USD',
            "player":localStorage.getItem('mdtUser'),
            "deckname":"noDeckYet"
        }
        
        await axios({
            method: 'post',
            url: api.freeUrl + '/pay',
            data: data, headers:headers
        }).then( (res) => {
            if (res.data.Status===true){
                this.setState({paypalResponseLink:res.data.Token})
            }else{
                this.setState({paypalResponseLink:'failed to generate paypal link =('})
            }
            
        } ).catch((e) => {
            console.log('erro no /pay')
            console.log(e)
            
        })
    }

    paypalButton = () => {
        if (localStorage.getItem('mdtUser')===null||localStorage.getItem('mdtUser')==undefined){
            return <></>
        }
        return <a target="popup" className={'deckPageButton float'} style={{fontSize: "23px",textDecoration:"none",color:"white",fontWeight:'400',border:'2px solid',borderColor:"lightgray"}} href={this.state.paypalResponseLink}><FontAwesomeIcon icon={["fab","paypal"]} style={{marginRight:'3px'}}/>{localStorage.getItem('countryCode')==='+55'?this.state.textsPt.paypalPay:this.state.textsEn.paypalPay}</a> 
    }

    paypalBox = () => {
        
        return (
            <div style={{textAlign:'center'}}>               
                {this.paypalButton()}
                <a target="popup" className={'deckPageButton float'} style={{fontSize: "23px",textDecoration:"none",color:"white",fontWeight:'400',border:'2px solid',borderColor:"lightgray"}} href={this.state.donationLinkPt}><FontAwesomeIcon icon={"heart"} style={{marginRight:'3px'}}/>{localStorage.getItem('countryCode')==='+55'?this.state.textsPt.payApoia:this.state.textsEn.payApoia}</a> 
               
               
                
                {this.paypalMessage()}

            </div>
        )
    }

    premiumWindow = () => {
        return(
            <div style={{margin:'25px',minHeight:"70vh"}}>
                <div style={{textAlign:'center',marginBottom:'2px',fontWeight:900}}> <h3>{localStorage.getItem("countryCode")==="+55"?"Se torne um parceiro do Commanderdecks.com por 1 ano!":"Become a Commanderdecks.com partner for a year!" }</h3> </div>
                    <div style={{textAlign:'center',marginBottom:'10px',marginLeft:'2px'}}> {localStorage.getItem("countryCode")==="+55"?"Como parceiro, ganha os seguintes poderes:":"As our partner, you will have more powers, like:" } </div>
                    

                    <div style={{marginRight:'15%',marginLeft:'15%',display:'grid',gridTemplateColumns:'auto auto'}}>
                        <div style={{textAlign:'center',marginBottom:'2px'}}> <div><FontAwesomeIcon icon={['fas','globe-europe']} size={window.innerWidth>700?"4x":"2x"}/></div><div><h3>{localStorage.getItem("countryCode")==="+55"?"Mantenha o Commanderdecks online":"Keep Commanderdecks online" }</h3> { window.innerWidth>700? localStorage.getItem("countryCode")==="+55"?"Ao se tornar parceiro, voc nos ajuda a manter o site online":"When you become our pal, you help us to stay online" :""} </div> </div>
                        <div style={{textAlign:'center',marginBottom:'2px'}}> <div><FontAwesomeIcon icon={['fas','glasses']} size={window.innerWidth>700?"4x":"2x"}/> </div> <div><h3>{localStorage.getItem("countryCode")==="+55"?"Seja Imune a ads":"Be imune to ads" }</h3> {window.innerWidth>700?localStorage.getItem("countryCode")==="+55"?"Propagandas no iro aparecer para voc!":"No more ads baby" :""} </div> </div>
                        <div style={{textAlign:'center',marginBottom:'2px'}}> <div><FontAwesomeIcon icon={['fas','book-open']} size={window.innerWidth>700?"4x":"2x"}/></div><div><h3>{localStorage.getItem("countryCode")==="+55"?"Promova seus decks":"Promote your decks" }</h3> {window.innerWidth>700?localStorage.getItem("countryCode")==="+55"?"Decks promovidos aparecem na homepage de todos os usurios!":"Decks on sale will appear on the homepage of every user!":"" }</div> </div>
                        <div style={{textAlign:'center',marginBottom:'2px'}}> <div><FontAwesomeIcon icon={['fas','search-dollar']} size={window.innerWidth>700?"4x":"2x"}/> </div> <div><h3>{localStorage.getItem("countryCode")==="+55"?"Descubra onde comprar":"Discover where to buy" }</h3> {window.innerWidth>700?localStorage.getItem("countryCode")==="+55"?"Descubra as lojas em que a sua compra tem custo mnimo (R$)":"Discover the stores where the decks cost are minimun ($)":"" } </div></div>
                    </div>
                    <div style={{color:'red',textAlign:'center',marginBottom:'10px',marginLeft:'2px'}}> {localStorage.getItem("countryCode")==="+55"?this.state.textsPt.apoiaseWarning:this.state.textsEn.apoiaseWarning } </div>
                    <div style={{color:'red',textAlign:'center',marginBottom:'10px',marginLeft:'2px'}}> {localStorage.getItem("countryCode")==="+55"?"Como coletamos os preos de fontes externas, pode ocorrer do servio de comprar deck parar de funcionar temporariamente devido a mudanas na fonte":"We collect the card prices from external resources, that means that the purchase deck feature can stop working at any moment, but when an external resource change we will adapt to collect again" } </div>
                    <div>                    
                        {this.paypalBox()}                   
                    </div>                
            </div>
        )
    }

    loadUser = () =>{
        const axiosConfig = api.axiosConfig;        
        let data = {
            "nickname": localStorage.getItem('mdtUser'),            
        }
        axios({
            method: 'post',
            url: api.freeUrl + '/player/get',
            data: data, headers:axiosConfig.headers
        }).then(res => {
            if (res.status === 200) {
                
                if (res.data.length>0){     
                    if (res.data[0].ispremium===true){
                        this.props.history.push('/')
                        document.location.reload()    
                    }             
                    this.setState({ispremium:res.data[0].ispremium,loadedUser:res.data[0]})
                
                }else{
                    this.setState({ispremium:false})
                    this.props.history.push('/')
                    document.location.reload()
                }            
            }
        }).catch( (err) => {
            this.setState({ispremium:false})
            this.props.history.push('/')
            document.location.reload()
        })
    }

    componentDidMount(){
        if (localStorage.getItem('mdtUser')!==null&&localStorage.getItem('mdtUser')!==undefined){
            this.getPaypalLink()
            this.loadUser()
        }else{
            this.props.history.push('/')
            document.location.reload()    
        }            
    }
    
    render(){
        if (this.state.problemOnLoad===true){
            return <ErrorPage/>
        }        
        try{
            return (
                <div>
                    <GlobalNavBar/>
                    {this.premiumWindow()}
                    <BottomBar/>
                </div>
            )
        }catch(e){
            
            if (this.state.problemOnLoad===true){
                return <ErrorPage></ErrorPage>
            }else{
                return <GlobalNavBar/>
            }
            
        }
        
    }
}
export default withRouter(BecomeProPage);
