    loadDisplayCommander = (token) => {
        if ((token.length)<3){
            return []
        }
        let cartas = []
        for (let card of this.state.allCommanders){
            if ( card.name.toUpperCase().includes(token.toUpperCase()) && cartas.length<=12 ){
                cartas.push(card);
            }
        }
        return cartas
    }

