            "addDeck":"Criar deck",
            "addFolder":"Criar pasta",
            "blockTitle":"Crie o seu primeiro deck",
            "nameNewFolder":"Coloque aqui o nome da sua pasta",
            "blockMessage":"Bem vindo a pgina de criao de decks, clique em criar deck para adicionar um novo baralho, categorize seus decks do seu jeito, faa playtests, receba recomendaes de cards, descubra onde comprar gastando menos dinheiro e muito mais.",
            "whyCategorizeTitle":"Porqu categorizar as cartas?",
